import React, { useState } from "react";
import {
  StyleSheet,
  Text,
  View,
  Dimensions,
  TextInput,
  Button,
} from "react-native";
import { useHistory } from "./react-router";

const Start = ({handlePlayerName}) => {

    const [name,setName] = useState('text your name');
    let history = useHistory();
    const goGame = ()=>{
        history.push("/game");
    };
    
  

  return (
    <View style={styles.container}>
      <View>
        <Text style={styles.text}>YOUR NAME</Text>
        <TextInput
          style={styles.textInput}
          onChangeText={(e) => {
            setName(e);
            handlePlayerName(e)
          }}
          value={name}
        
        />
      </View>
      <View>
        <Button
          title="Start"
          color="#841584"
          accessibilityLabel="Start the game"
          onPress={()=>{goGame()}}
        />
      </View>
    </View>
  );
};

export default Start;

const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#f5f4e9",
    alignItems: "center",
    justifyContent: "space-around",
    borderWidth: 1,
    height: windowHeight,
    width: windowWidth,
  },
  text: {
    color: "#888",
    textAlign: "center",
    fontSize: 26,
  },

  textInput: {
    fontSize: 19,
    width: windowWidth / 2,
    height: 40,
    borderColor: "#888",
    borderWidth: 1,
    backgroundColor: "#ccca9b",
  },
});
