import React, { useState } from "react";
import {
  StyleSheet,
  Text,
  View,
  Dimensions,
  TextInput,
  Button,
} from "react-native";
import { MaterialCommunityIcons } from "@expo/vector-icons";

const TopGame = ({ money, day, loan ,happy, health,playerName }) => {
  return (
    <View style={styles.container}>
      <View style={styles.topContainer}>
        <Text>{playerName}</Text>
        <Text> is the {day}day</Text>
        <Text> </Text>
      </View>
      <View style={styles.bottomContainer}>
        <View style={styles.infoTitle}>
          <MaterialCommunityIcons name="cash" size={24} color="black" />
          <Text>money{"\n"}{money}</Text>
        </View>
        <View style={styles.infoTitle}>
          <MaterialCommunityIcons name="cash" size={24} color="#c7c7b3" />
          <Text>loan  {"\n"}{loan}</Text>
        </View>
        <View style={styles.infoTitle}>
          <MaterialCommunityIcons name="heart" size={24} color="black" />
          <Text>health{"\n"}{health}</Text>
        </View>
        <View style={styles.infoTitle}>
          <MaterialCommunityIcons
            name="emoticon-happy-outline"
            size={24}
            color="black"
          />
          <Text>happy{"\n"}{happy}</Text>
        </View>
      </View>
    </View>
  );
};
export default TopGame;

const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;
const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#f7f7cd",
    alignItems: "center",
    justifyContent: "space-around",
   // borderWidth: 1,
    flexDirection: "column",
  },
  topContainer: {
    flex: 1,
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-between",
    
  },
  bottomContainer: {
    flex: 3,
    flexDirection: "row",
    flexWrap: "wrap",
    justifyContent: "space-around",
  },
  infoTitle: {
    flex: 1,
   // borderWidth: 1,
    minWidth: (windowWidth * 3) / 8,
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "row",
    backgroundColor:"#e3e2ba",
  },
});
