import React, { useState, useEffect } from "react";
import {
  StyleSheet,
  Text,
  View,
  Dimensions,
  TextInput,
  Button,
  FlatList,
} from "react-native";
import { Router, Route, Link, Switch, useHistory } from "./react-router";
import { Ionicons } from "@expo/vector-icons";
import { MaterialCommunityIcons } from "@expo/vector-icons";

const MainGame = ({
  money,
  products,
  setMoney,
  setLoan,
  loan,
  happy,
  setHappy,
  health,
  setHealth,
}) => {
  let history = useHistory();
  const goStore = () => {
    history.push("/game/store");
  };
  const goMarket = () => {
    history.push("/game/");
  };
  const goOthers = () => {
    history.push("/game/others");
  };

  const [alertShow, setAlertShow] = useState(true);

  const [nowProducts, setNowProducts] = useState(); //the produces price==["p1",wholeprice,number]
  const [singlePrice, setSinglePrice] = useState(); //products singleprice
  const [productNumber, setProductNumber] = useState(1); //the number of produces==number
  const [storeProduct, setStoreProduct] = useState([["pencil", 10, 10]]); //store ===[["p1","whole price","number"]   ]

  const [testState, setTestState] = useState(1);

  const clickBuy = (e) => {
    setAlertShow(!alertShow);
  // setNowProducts([e.item[0], e.item[1]]);
    //console.log(e.item);
    setSinglePrice([e.item[1]]);
    setNowProducts([e.item[0], e.item[1] * productNumber, productNumber])
    console.log("this is productNumber" + productNumber);
    //console.log("this is test " + testState);
  };

  const handleProductNumber = (e) => {
    if (e > 0) {
      console.log(nowProducts);
      console.log(typeof Number(e));
      setProductNumber(Number(e));
      setNowProducts([nowProducts[0], singlePrice * Number(e), Number(e)]);
      //console.log(e);
    } else {
      setProductNumber(e);
    }
    console.log("this is storeProduct" + storeProduct);
  };

  const confirmBuy = () => {
    // setTestState(testState + 10);
    //console.log("this is test " + testState);
    //setNowProducts([...nowProducts,productNumber]);
    // console.log(nowProducts);
    if (money < nowProducts[1]) {
      alert("you dont have enough money");
    } else {
      // console.log(nowProducts);
      setMoney(money - singlePrice*productNumber);

      checkStore();

      setAlertShow(!alertShow);
      console.log("this is storeProduct" + storeProduct);
      console.log("this is productNumber" + productNumber);
    }
  };

  const checkStore = () => {
    let tempStore = JSON.parse(JSON.stringify(storeProduct));
    console.log(tempStore);
    if (tempStore.some((element) => element[0] === nowProducts[0])) {
      for (let i = 0; i < tempStore.length; i++) {
        if (tempStore[i][0] === nowProducts[0]) {
          //console.log(i);
          tempStore[i][1] = tempStore[i][1] + nowProducts[1];
          tempStore[i][2] = tempStore[i][2] + productNumber;
          setStoreProduct(tempStore);
        }
      }
    } else {
      setStoreProduct([
        ...storeProduct,
        [nowProducts[0], nowProducts[1], productNumber],
      ]);
    }
  };

  const showAlert = () => {
    if (alertShow) {
      return null;
    }
    return (
      <View style={styles.alertStyle}>
        <Text>this price is 89 </Text>
        <TextInput
          style={{ height: 40, borderColor: "gray", borderWidth: 1 }}
          onChangeText={(e) => handleProductNumber(e)}
          value={`${productNumber}`}
          maxLength={5}
        />
        <View style={styles.alertButton}>
          <Button
            onPress={confirmBuy}
            title="comfirm"
            color="#ebe860"
            accessibilityLabel="i wanna buy"
          />
          <Button
            onPress={() => {
              setAlertShow(!alertShow);
            }}
            title="cancel"
            color="#d6d6bc"
            accessibilityLabel="i dont buy"
          />
        </View>
      </View>
    );
  };

  const [alertSellShow, setAlertSellShow] = useState(true);
  const [sellProduct, setSellProduct] = useState(); //["the good","single price","the whole number"]
  const [sellNumber, setSellNumber] = useState(1); //[just sell nnumber]

  const clickSellTest = () => {
    console.log("this is storeProduct" + storeProduct);
  };

  const clickSell = (e) => {
    setAlertSellShow(!alertSellShow);
    setSellProduct([e.item[0], e.item[1]]);
  };

  const handleSellNumber = (e) => {
    if (e >= 0) {
      setSellNumber(Number(e));
    }
    console.log(typeof sellNumber);
  };

  const confirmSell = () => {
    let tempSellList = JSON.parse(JSON.stringify(storeProduct));
    // console.log("this is temp"+ tempSellList);
    if (tempSellList.some((element) => element[0] === sellProduct[0])) {
      for (let i = 0; i < tempSellList.length; i++) {
        if (tempSellList[i][0] === sellProduct[0]) {
          if (sellNumber > tempSellList[i][2]) {
            alert("you dont have enough goods");
          } else {
            tempSellList[i][1] =
              tempSellList[i][1] - sellNumber * sellProduct[1];
            tempSellList[i][2] = tempSellList[i][2] - sellNumber;
            /*
          //err: the second callback argument
            setSellProduct(
              sellProduct[0],
              sellProduct[1],
              sellNumber * sellProduct[1]
            );
            */
            setMoney(money + sellNumber * sellProduct[1]);
            setAlertSellShow(!alertSellShow);
            setStoreProduct(tempSellList);
          }
        }
      }
    } else {
      alert("you has not good");
    }
  };

  const showSellAlert = () => {
    if (alertSellShow) {
      return null;
    }
    return (
      <View style={styles.alertStyle}>
        <Text>this price sell is 89 </Text>
        <TextInput
          style={{ height: 40, borderColor: "gray", borderWidth: 1 }}
          onChangeText={(e) => handleSellNumber(e)}
          value={`${sellNumber}`}
          maxLength={5}
        />
        <View style={styles.alertButton}>
          <Button
            onPress={() => {
              confirmSell();
            }}
            title=" Sell "
            color="#ebe860"
            accessibilityLabel="i wanna Sell"
          />
          <Button
            onPress={() => {
              setAlertSellShow(!alertSellShow);
            }}
            title="Cancel"
            color="#d6d6bc"
            accessibilityLabel="i dont Sell"
          />
        </View>
      </View>
    );
  };

  const [alertOtherShow, setAlertOtherShow] = useState(true);
  const [otherNumber, setOtherNumber] = useState(1);
  const [otherItem, setOtherItem] = useState();
  const [showInfo, setShowInfo] = useState();

  const handleOtherNumber = (e) => {
    if (e >= 0) {
      setOtherNumber(Number(e));
    }
  };

  const handleOther = (e) => {
    setAlertOtherShow(false);
    setOtherItem(e);
    if (e === 2) {
      setShowInfo("you can back money or loan maoney");
    } else if (e === 1) {
      setShowInfo("200 can make your health a little bit");
    } else if (e === 3) {
      setShowInfo("donate 200 can add 1 point happiness");
    } else {
      setShowInfo("10000 can add 1 store space");
    }
  };

  const confirmOther = () => {
    if (otherItem === 2) {
      if (otherNumber > money) {
        alert("you dont have enought to pay");
      } else if (loan <= 0) {
        alert("you dont need to pay");
      } else if (otherNumber > loan) {
        alert("you donnt need pay much");
      } else {
        setLoan(loan - otherNumber);
        setMoney(money - otherNumber);
        setAlertOtherShow(!alertOtherShow);
      }
    } else if (otherItem === 3) {
      if (otherNumber * 200 > money) {
        alert("you dont have enought to pay");
      } else {
        setHappy(happy + otherNumber);
        setMoney(money - otherNumber * 200);
        setAlertOtherShow(!alertOtherShow);
      }
    } else if (otherItem === 1) {
      if (otherNumber * 200 > money) {
        alert("you dont have enought to pay");
      } else {
        setHealth(health + otherNumber);
        setMoney(money - otherNumber * 200);
        setAlertOtherShow(!alertOtherShow);
      }
    }else{
      alert("the estate function is building,so you have unlimited space")
    }
  };

  const showOtherAlert = () => {
    if (alertOtherShow) {
      return null;
    }
    return (
      <View style={styles.alertStyle}>
        <Text>{showInfo}</Text>
        <TextInput
          style={{ height: 40, borderColor: "gray", borderWidth: 1 }}
          onChangeText={(e) => handleOtherNumber(e)}
          value={`${otherNumber}`}
          maxLength={5}
        />
        <View style={styles.alertButton}>
          <Button
            onPress={() => {
              confirmOther();
            }}
            title="confirm"
            color="#ebe860"
            accessibilityLabel="i wanna Sell"
          />
          <Button
            onPress={() => {
              setAlertOtherShow(!alertOtherShow);
            }}
            title="cancel"
            color="#d6d6bc"
            accessibilityLabel="i dont Sell"
          />
        </View>
      </View>
    );
  };

  return (
    <View style={styles.container}>
      {showAlert()}
      {showSellAlert()}
      {showOtherAlert()}
      <View style={styles.topContainer}>
        <Text
          onPress={() => {
            goStore();
          }}
        >
          <MaterialCommunityIcons name="store" size={24} color="black" />
          Store
        </Text>
        <Text
          onPress={() => {
            goMarket();
          }}
        >
          <MaterialCommunityIcons name="cart" size={24} color="black" />
          Market
        </Text>
        <Text
          onPress={() => {
            goOthers();
          }}
        >
          <MaterialCommunityIcons name="run" size={24} color="black" />
          Others
        </Text>
      </View>
      <View style={styles.mainContainer}>
        <Switch>
          <Route path="/game/store">
            <Store storeProduct={storeProduct} clickBuy={clickBuy} />
          </Route>
          <Route exact path="/game/">
            <Market
              products={products}
              clickBuy={clickBuy}
              clickSell={clickSell}
              clickSellTest={clickSellTest}
              storeProduct={storeProduct}
            />
          </Route>
          <Route path="/game/others">
            <Others handleOther={handleOther} />
          </Route>
        </Switch>
      </View>
    </View>
  );
};
export default MainGame;

const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;
const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#f7f7cd",
    alignItems: "center",
    justifyContent: "space-around",
    //borderWidth: 1,
    flexDirection: "column",
  },
  topContainer: {
    flex: 1,
   // borderWidth: 1,
    justifyContent: "space-around",
    alignItems: "center",
    flexDirection: "row",
    width: windowWidth,
  },
  threeChooseContainer: {
    flex: 1,
  },
  mainContainer: {
    flex: 8,
    //borderWidth: 1,
  },
  storeContainer: {
    backgroundColor: "#f5f4e9",
  },
  storeItem: {
    //fontSize: 17,
    //borderWidth: 1,
    flex: 1,
    width: windowWidth,
    minHeight: 33,
    flexDirection: "row",
    //justifyContent: "center",
    alignItems: "center",
  },
  storeItems: {
    flex: 1,
    textAlign: "center",
  },
  storeTitle: {
    flexDirection: "row",
    justifyContent: "space-evenly",
    minWidth: windowWidth,
  },
  alertStyle: {
    //borderWidth: 1,
    minHeight: windowHeight / 4,
    minWidth: windowWidth / 2,
    zIndex: 999,
    position: "absolute",
    justifyContent: "space-between",
    alignItems: "center",
    backgroundColor: "#dbdbc1",
  },
  alertButton: {
    flexDirection: "row",
  },
  otherContainer: {
    flex: 1,
    flexWrap: "wrap",
    flexDirection: "row",
  },
  otherItem: {
    flex: 1,
    //borderWidth: 1,
    minWidth: windowWidth / 2,
    justifyContent: "center",
    alignItems: "center",
  },
});

const Store = ({ products, clickBuy, storeProduct }) => {
  return (
    <View style={styles.storeContainer}>
      <View style={styles.storeTitle}>
        <Text style={styles.storeItems}>things</Text>
        <Text style={styles.storeItems}>price</Text>
        <Text style={styles.storeItems}>number</Text>
      </View>
      <FlatList
        data={storeProduct}
        renderItem={({ item }) => (
          <View style={styles.storeItem}>
            <Text
              onPress={() => {
                clickBuy({ item });
              }}
              style={styles.storeItems}
            >
              {item[0]}
            </Text>
            <Text style={styles.storeItems}>{parseInt(item[1]/item[2])}</Text>
            <Text style={styles.storeItems}>{item[2]}</Text>
          </View>
        )}
      />
    </View>
  );
};

const Market = ({
  products,
  handleBuying,
  clickBuy,
  clickSell,
  clickSellTest,
  storeProduct,
}) => {
  return (
    <View style={styles.storeContainer}>
      <View style={styles.storeTitle}>
        <Text style={styles.storeItems}>things</Text>
        <Text style={styles.storeItems}>price</Text>
        <Text style={styles.storeItems}>difference</Text>
      </View>
      <FlatList
        data={products}
        renderItem={({ item }) => (
          <View style={styles.storeItem}>
            <Text
              onPress={() => {
                clickBuy({ item });
              }}
              style={styles.storeItems}
            >
              {item[0]}
            </Text>
            <Text style={styles.storeItems}>{item[1]}</Text>
            <Text
              onPress={() => {
                clickSell({ item });
              }}
              style={styles.storeItems}
            >
              {item[2]}
            </Text>
          </View>
        )}
      />
    </View>
  );
};

const Others = ({ handleOther }) => {
  return (
    <View style={styles.otherContainer}>
      <View style={styles.otherItem}>
        <Text onPress={() => handleOther(1)}>Hospital</Text>
        <MaterialCommunityIcons
          name="hospital-building"
          size={44}
          color="black"
        />
      </View>
      <View style={styles.otherItem}>
        <Text onPress={() => handleOther(2)}>Bank</Text>
        <MaterialCommunityIcons name="bank" size={44} color="black" />
      </View>
      <View style={styles.otherItem}>
        <Text onPress={() => handleOther(3)}>Donate</Text>
        <MaterialCommunityIcons name="hospital-box" size={44} color="black" />
      </View>
      <View style={styles.otherItem}>
        <Text onPress={() => handleOther(4)}>Estate</Text>
        <MaterialCommunityIcons name="warehouse" size={44} color="black" />
      </View>
    </View>
  );
};
