import React, { useState, useEffect } from "react";
import { StyleSheet, Text, View, Dimensions } from "react-native";

const BottomGame = ({clickNextDay}) => {

    const [pointPosition,setPointPosition] = useState(windowWidth*2/6-windowWidth/12-6);

    const choosePosition=(e)=>{
        setPointPosition(windowWidth*e/6-windowWidth/12-6);
        clickNextDay();
    };


  return (
    <View style={styles.container}>
      <View style={styles.nameCity}>
      <Text style={styles.citys}> </Text>
        <Text style={styles.citys} onPress={()=>{choosePosition(2)}}>NewYork</Text>
        <Text style={styles.citys}> </Text>
        <Text style={styles.citys} onPress={()=>{choosePosition(4)}}>Trenton</Text>
        
        <Text style={styles.citys} > </Text>
        
        <Text style={styles.citys} onPress={()=>{choosePosition(6)}}>Hartford</Text>
        
      </View>

      <View style={styles.sliderStyle}>
        <View style={[styles.point, { marginLeft: pointPosition }]}></View>
      </View>

      <View style={styles.nameCity}>
      
        
        <Text style={styles.citys} onPress={()=>{choosePosition(1)}}>Boston</Text>
        
        <Text style={styles.citys} > </Text>
       
        <Text style={styles.citys} onPress={()=>{choosePosition(3)}}>Albany</Text>
        <Text style={styles.citys}> </Text>
        <Text style={styles.citys} onPress={()=>{choosePosition(5)}}>Concord</Text>
        <Text style={styles.citys}> </Text>
      </View>
    </View>
  );
};
export default BottomGame;


const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;
const styles = StyleSheet.create({
  container:{
    flex:1
  },
  sliderStyle: {
    width: windowWidth,
    borderWidth: 0.1,
    height: 6,
    backgroundColor: "grey",
    justifyContent: "center",
  },
  point: {
    width: 12,
    height: 6,
   // borderWidth: 1,
    borderRadius: 55,
    backgroundColor: "yellow",
  },
  nameCity: {
    flex:1,
    flexDirection: "row",
    justifyContent:"center",
    alignItems:"center",
  },
  citys:{
     //borderWidth:0.1,
      flex:1,
      textAlign:"center",
      minWidth: windowWidth/10,
      justifyContent:"center",
      alignItems:"center",
      //height:22,
      
  },cityText:{
    //borderWidth:1,
  }
  
});
