import React, { useState } from "react";
import {
  StyleSheet,
  Text,
  View,
  Dimensions,
  TextInput,
  Button,
  TouchableOpacity
} from "react-native";
import { useHistory } from "./react-router";
import TopGame from "./TopGame";
import MainGame from "./MainGame";
import BottomGame from "./BottomGame";

const Game = ({ playerName }) => {
  const [day, setDay] = useState(1);
  const [money, setMoney] = useState(10000);

  const [loan, setLoan] = useState(5000);
  const [health, setHealth] = useState(100);
  const [happy, setHappy] = useState(100);

  const handleDay = () => {
    setDay(day + 1);
    console.log(day);
  };

  let originProducts = [
    ["pencil",10,0],
    ["book",20,0],
    ["bag",300,0],
    ["phone",1000,0],
    ["ring",3000,0],
    ["gun",6000,0],
    ["moto",10000,0],
    ["car",20000,0],
    ["plane",90000,0],
  ];

  const [products, setProducts] = useState(originProducts);

  // learn about deep array copy
  const handleProducts = () => {
    let aaa = JSON.parse(JSON.stringify(originProducts));
    for (let i = 0; i < aaa.length; i++) {
      aaa[i][1] = Math.floor(aaa[i][1] * (Math.random() + 0.5));
    };

    let a = Math.random();
    if(a<0.4){
      setAlertRandomShow(false);
    };
    if (a < 0.05) {
      aaa[0][1] = aaa[0][1] * 2;
      setAlertRandomMessage("the pencil industry fired,the pencil price is doubled");
      
    }else if(a<0.1){
      aaa[1][1]= aaa[1][1]*2;
      setAlertRandomMessage("new wood law created,the book price is doubled ")
    }else if(a<0.15){
      aaa[2][1]= aaa[1][1]*2;
      setAlertRandomMessage("having bag is popular now,the bag price is doubled ")
    }else if(a<0.2){
      aaa[3][1]= aaa[1][1]*2;
      setAlertRandomMessage("new Iphone is published,the phone price is doubled ")
    }else if(a<0.25){
      aaa[4][1]= aaa[1][1]*2;
      setAlertRandomMessage("the gold price is raise,the ring price is doubled ")
    }else if(a<0.3){
      aaa[5][1]= aaa[1][1]*2;
      setAlertRandomMessage("people need feel safe,the gun price is doubled ")
    }else if(a<0.35){
      aaa[6][1]= aaa[1][1]*2;
      setAlertRandomMessage("younger like moto,the moto price is doubled ")
    }else if(a<0.4){
      aaa[8][1]= aaa[1][1]*2;
      setAlertRandomMessage("new law will reduce the amounts of car,the car price is doubled ")
    };



    setProducts(aaa);
    
  };

  

  const handleLoan = () => {
    setLoan(parseInt(loan * 1.05));
  };
  const handleHappy = ()=>{
    setHappy(happy-1)
  };
  const handleHealth = ()=>{
    setHealth(health-1)
  };

  const clickNextDay = () => {
    handleDay();
    handleProducts();
    handleLoan();
    handleHappy();
    handleHealth();
  };

  const [alertRandomShow,setAlertRandomShow] = useState(true);
  const [alertRandomMessage,setAlertRandomMessage] = useState("this is random message");

  const showRandomAlert = () => {
    if (alertRandomShow) {
      return null;
    }
    return (
      <View style={styles.alertStyle}>

        
          <Text>NEWS</Text>
          <Text>{alertRandomMessage} </Text>
          <TouchableOpacity onPress={() => {
              setAlertRandomShow(true);
            }} style={styles.alertButton}>
           <Text>OK</Text>
          </TouchableOpacity>
       
      </View>
    );
  };

  return (
    <View style={styles.container}>
      
      <View style={styles.topContainer}>
        <TopGame
          playerName={playerName}
          money={money}
          day={day}
          loan={loan}
          health={health}
          happy={happy}
        />
      </View>
      <View style={styles.middleContainer}>
      {showRandomAlert()}
        <MainGame
          products={products}
          setMoney={setMoney}
          money={money}
          setLoan={setLoan}
          loan={loan}
          setHealth={setHealth}
          health={health}
          setHappy={setHappy}
          happy={happy}
        />
      </View>
      <View style={styles.bottomContainer}>
        <BottomGame clickNextDay={clickNextDay} />
      </View>
    </View>
  );
};
export default Game;

const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;
const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#f5f4e9",
    alignItems: "center",
    justifyContent: "space-around",
   // borderWidth: 1,
    height: windowHeight,
    width: windowWidth,
  },
  topContainer: {
    flex: 2,
   // borderWidth: 1,
    width: windowWidth,
    flexDirection: "row",
  },
  middleContainer: {
    flex: 5,
   // borderWidth: 1,
    width: windowWidth,
  },
  bottomContainer: {
    flex: 1,
   // borderWidth: 1,
    width: windowWidth,
    justifyContent: "space-around",
  },
  alertStyle:{
    position: "absolute",
    zIndex:999,
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "#dbdbc1",
    borderRadius:6,
    width:"80%",
    marginTop:"30%",
    marginBottom:"50%",
    marginLeft:"10%",
    marginRight:"10%",
  
  },
  alertButton: {
    position:"relative",
    width:"100%",
   // borderRadius:6,
    marginTop: 10,
    borderBottomEndRadius:6,
    borderBottomStartRadius:6,
    backgroundColor:"#e3e2ba",
    justifyContent:"center",
    alignItems:"center",
  }
});
